:root { 
  --blue: #1DA1F2; 
  --dark-blue: #0086b3; 
  --light-blue: #e8f4f8; 
  --black: #14171A; 
  --dark-gray: #657786; 
  --light-gray: #AAB8C2; 
  --extra-light-gray: #E1E8ED; 
  --extra-extra-light-gray: #F5F8FA; 
}

* { 
  box-sizing: border-box; 
}

body, a, ul { 
  font-family: 'Open Sans', sans-serif;
  margin: 0; 
  text-decoration: none; 
  list-style-type: none;
}


.login-container { 
  display: flex; 
  height: 100vh;
}

.twitter-login-image-container { 
  flex-grow: 1; 
  flex-basis: 0;
  overflow: hidden; 
}

.twitter-login-welcome { 
  flex-grow: 1; 
  flex-basis: 0; 
  display: flex; 
  flex-direction: column; 
  justify-content: center;  
}

.login { 
  margin-left: 30px; 
  display: flex; 
  flex-direction: column; 
}

.twitter-logo-main { 
  height: 300px;
  width: 300px; 
  top: 40%; 
  left: 15%; 
  z-index: 1;
  position: absolute;
} 

.twitter-logo-small { 
  height: 40px;
  width: 40px; 
}

.twitter-login-header  {
  font-size: 62px; 
  font-weight: 800; 
}

.sign-up-notice { 
  font-size: 12px; 
}


.login-button { 
  font-family: sans-serif; 
  font-weight: bold;
  color: var(--blue); 
  border: 1px solid var(--blue); 
  border-radius: 25px; 
  padding: 10px; 
  background-color: white; 
  width: 350px; 
  height: 45px; 
  margin: 7px 0; 
}

.sign-up-button { 
  font-family: sans-serif; 
  font-weight: bold;
  color: white; 
  border: 1px solid var(--blue); 
  border-radius: 20px; 
  padding: 10px; 
  background-color: var(--blue); 
  width: 350px; 
  height: 45px; 
  margin: 7px 0 ; 
}

.login-button:hover { 
  background-color: var(--light-blue); 
}

.sign-up-button:hover { 
  background-color: var(--dark-blue); 
}

@media only screen and (max-width: 1000px){ 
  .login-container { 
    flex-direction: column-reverse; 
  }
  .twitter-login { 
    display: block; 
    width: 100%;  
  }

  .twitter-logo-main { 
    display: none; 
  } 
}
.button { 
  font-family: sans-serif; 
  font-weight: bold;
  border-radius: 25px;
  padding: 10px;   
  margin: 7px 0; 
}

.button:focus { 
  outline: none; 
}

.small { 
  width: 90px;
  height: 35px; 
  padding: 8px;  
}

.medium { 
  width: 200px;
  height: 40px; 
  
}

.large { 
  width: 350px; 
  height: 45px; 
}

.dark { 
  color: white; 
  border: 1px solid var(--blue); 
  background-color: var(--blue); 
}

.light { 
  color: var(--blue); 
  border: 1px solid var(--blue); 
  background-color: white; 
}

.dark:hover { 
  background-color: var(--dark-blue); 
}

.light:hover { 
  background-color: var(--light-blue); 
}

@media only screen and (max-width: 1150px){ 
  .button.medium { 
    width: 100px; 
  }
}

@media only screen and (max-width: 850px){ 
  .button.medium { 
    display: none; 
  }
}
.home-container  { 
  display: flex; 
}

.home-side-nav-container { 
  flex-grow: 1; 
  flex-basis: 0;
  display: flex; 
  justify-content: flex-end; 
}


.home-header-container { 
  flex-grow: 1; 
  flex-basis: 0
}

.home-header { 
  display: inline-block; 
  margin: 0; 
  width: 600px;
  padding: 20px;
  border-left: 1px solid var(--extra-light-gray);
  border-right: 1px solid var(--extra-light-gray);
}

.home-search-container { 
  flex-grow: 1; 
  flex-basis: 0
}

@media only screen and (max-width: 1300px){ 
  /* .home-search-container { 
    display: none;  
  } */
}

.nav-bar-items { 
  display: inline-block; 
  padding: 30px 70px;
}

.logo { 
  height: 20px; 
  margin-left: 10px;
}

.logo-container { 
  width: 50px; 
}

.nav-bar-item { 
  padding: 12px 0; 
}

.nav-bar-item:hover { 
  background-color: var(--light-blue); 
  border-radius: 30px; 
}

.link { 
  color: black; 
  font-weight: 700; 
  font-size: 19px; 
  display: flex; 
  align-items: center; 
}

.active { 
  color: var(--blue);
}

.nav-icon { 
  color: black; 
  margin-left: 10px;
}

.link.active > span.nav-icon{ 
  color: var(--blue);
}

.nav-text { 
  margin: 0 0 0 20px; 
}


@media only screen and (max-width: 1150px){ 
  .nav-bar-items { 
    display: flex; 
    flex-direction: column;
    padding: 30px 70px;
  }
  .nav-bar-item { 
    width: 50px; 
    display: inline-block;
  }
  .nav-text { 
    display: none;
  }
  
}

@media only screen and (max-width: 850px){ 
  .nav-bar-items { 
    padding: 0
  }
}

.modal {
  position: fixed; /* Stay in place */
  z-index: 1; /* Sit on top */
  left: 0;
  top: 0;
  width: 100%; /* Full width */
  height: 100%; /* Full height */
  background-color: rgb(0,0,0); /* Fallback color */
  background-color: rgba(0,0,0,0.4); /* Black w/ opacity */
}

/* modal Content/Box */
.modal-content {
  background-color: #fefefe;
  margin: 15% auto; /* 15% from the top and centered */
  padding: 20px;
  border: 1px solid #888;
  width: 650px; /* Could be more or less, depending on screen size */
}

.close-button { 
  color: var(--blue)
}

.close-button:hover { 
  cursor: pointer;
}
.new-tweet-container { 
  display: inline-flex; 
  flex-direction: column; 
  justify-content: space-between; 
  width: 600px;
  border: 1px solid var(--extra-light-gray); 

}

.new-tweet-input { 
  border: 0; 
  margin: 20px 20px 0;
  height: 50px;
  overflow: hidden;
  resize: none; 
}

.new-tweet-input:focus { 
  outline: none; 
}

.new-tweet-button-container { 
  display: flex; 
  justify-content: flex-end; 
  padding: 20px 20px 10px;
}

/* .button-container { 
  padding: 20px; 
} */
.search-container { 
  padding-left: 30px; 
  width: 350px;
}

.search-input-container { 
  height: 76px;
  display: flex;
  align-items: center;
}

.search-input-form { 
  display: flex; 
  width: 350px; 
  height: 45px;
  border-radius: 20px; 
  background-color: var(--extra-light-gray); 
  align-items: center; 
}

.search-input { 
  border: none; 
  background-color: var(--extra-light-gray); 
  height: 20px; 
  font-size: 15px; 
  width: 80%; 
}

.search-input:focus { 
  outline: none; 
  background-color: white; 
}

.search-icon { 
  color: var(--light-gray); 
  height: 20px;
  margin-left: 15px; 
}


.search-input-form:focus-within { 
  background-color: white; 
  border: 1px solid var(--blue); 
}

.search-input-form:focus-within > .search-icon{ 
  color: var(--blue); 
}

@media only screen and (max-width: 1300px){ 
  .search-container { 
    width: 250px; 
    /* display: none; */
  }
  
}

@media only screen and (max-width: 1200px){ 
  .search-container { 
    width: 200px; 
    /* display: none; */
  }
  .search-input { 
    width: 70%; 
  }
}

@media only screen and (max-width: 1050px){ 
  .search-container { 
    display: none;
  }
}

.trending-tweets-list { 
  display: inline-block; 
  padding: 0; 
  width: 100%;
}

.trending-top { 
  padding: 20px; 
  border-top-left-radius: 20px; 
  border-top-right-radius: 20px; 
  background-color: var(--extra-extra-light-gray); 
}

.trending-top > h3 { 
  margin: 0; 
}

.trending-bottom { 
  padding: 20px; 
  border-top: 1px solid var(--extra-light-gray);
  border-bottom-left-radius: 20px; 
  border-bottom-right-radius: 20px; 
  background-color: var(--extra-extra-light-gray); 
}
.trending-info { 
  font-size: 13px; 
  color: var(--light-gray);
}

.trending-tweet { 
  width: 100%;
  background-color: var(--extra-extra-light-gray); 
  padding: 10px; 
  border-top: 1px solid var(--extra-light-gray);
}

.trending-tweet:hover { 
  background-color: var(--extra-light-gray);
}

.trending-tweet-text { 
  font-weight: 700; 
  margin: 0; 
}
.loader-container {
  padding-top: 20px; 
  display: flex; 
  justify-content: center; 
}

.loader {
  display: inline-block;
  border: 3px solid #f3f3f3;
  border-radius: 50%;
  border-top: 3px solid #3498db;
  width: 30px;
  height: 30px;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.large-spinner { 
  width: 120px;
  height: 120px;
}
.error-message { 
  display: flex; 
  flex-direction: column; 
  justify-content: center; 
  align-items: center; 
  height: 100px;
  border: 2px dashed red;
}
.tweet-container { 
  border-bottom: .5px solid var(--extra-light-gray); 
  border-right: .5px solid var(--extra-light-gray);
  border-left: .5px solid var(--extra-light-gray);
  display: flex; 
}

.tweet-content { 
  width: 100% 
}

.tweet-container:hover {
  background-color: var(--extra-extra-light-gray);
}


.user-profile-image { 
  padding: 15px 10px 0 15px; 
}


.profile-image { 
  border-radius: 50px; 
  height: 48px; 
