}

.tweet-info { 
  display: flex; 
  justify-content: space-around; 
  color: var(--light-gray); 
}

.icon { 
  margin-right: 10px; 
  color: var(--light-gray);
}


.retweets-container { 
  height: 55px; 
  width: 65px; 
}
.retweets-container:hover { 
  color: var(--blue); 

 
}

.retweets-container:hover > .retweets{ 
  color: var(--blue); 
}

.likes-container:hover { 
